import NewFieldModel from '@/views/field/model/NewFieldModel';
import ManagerInfoModel from '@/views/field/model/ManagerInfoModel';
import AreaTypeInfoModel from '@/views/field/model/AreaTypeInfoModel';
import AreaTypeInfoViewModel from '@/views/field/viewModel/AreaTypeInfoViewModel';
import BuildingInfoModel from '@/views/field/model/BuildingInfoModel';
import BuildingInfoViewModel from '@/views/field/viewModel/BuildingInfoViewModel';
import HouseHoldInfoModel from '@/views/field/model/HouseHoldInfoModel';
import HouseHoldInfoViewModel from '@/views/field/viewModel/HouseHoldInfoViewModel';

import { store } from '@/store';

import { make00 } from '@/utils/stringUtils'

export default class NewFieldViewModel {
  constructor() {
    //  model
    this.model = {};
    //   
    this.newFieldInfoView = [
      {
        foldTitle:'',
        foldDataList : [
          { 
            title: '',
            componentName:'Input',
            value: 'fieldName',
            placeholder:' ',
            isEssential: true
          },
          { 
            title: '',
            componentName:'InputSearch',
            value: 'fieldAddress',
            placeholder:'  ',
            isEssential: true,
            isHasMap: true,
          },
          {
            title: '()',
            componentName:'InputSelectNumber',
            value: 'buildingCount',
            placeholder:'0',
            isEssential: true,
          },
          {
            title: ' ',
            componentName:'InputSelectNumber',
            value: 'areaTypeCount',
            maxValue: 10,
            placeholder:'0',
            isEssential: true,
            descTip:' 10 '
          },
          {
            title: ' ',
            componentName:'InputUnit',
            value: 'houseHoldsCount',
            unit:'',
            isEssential: true,
          },
          { 
            title: '  ',
            componentName:'Input',
            value: 'fieldOpenDate',
            placeholder:'   ',
            isEssential: false,
          },
          { 
            title: ' ',
            componentName:'Input',
            value: 'completionDate',
            placeholder:'  ',
            isEssential: false,
          },
        ]
      },
      {
        foldTitle:' ',
        foldRight:[
          { //   
            isCustom: true,
            customName:'managerAddBtn',
          }
        ],
        foldDataList: [
          { 
            title: ' ',
            componentName:'Input',
            value: 'ceoName',
            placeholder:'  ',
            isEssential: false,
            halfAlign:'l',
          },
          { 
            title: ' ',
            componentName:'Input',
            value: 'ceoPhone',
            placeholder:'-  ',
            isEssential: false,
            halfAlign:'r',
          },
          { //  
            isCustom: true,
            customName:'managerInfoDataList',
          },
          { 
            title: '',
            componentName:'Input',
            value: 'request',
            placeholder:'   ',
            isEssential: false
          },
        ]
      }
    ];
    this.isShowMap = false; //     

    //  ViewModel
    this.AreaTypeInfoViewModel = new AreaTypeInfoViewModel();
    this.BuildingInfoViewModel = new BuildingInfoViewModel();
    this.HouseHoldInfoViewModel = new HouseHoldInfoViewModel();

    //    step
    this.stepDataList = [
      { index: 1, name: '', id: 'InfoWrite', validateDataList:['fieldName','fieldAddress','buildingCount','areaTypeCount','houseHoldsCount'] },
      { index: 2, name: '/  ', id: 'DetailWrite' },
    ];
    //    step -  step
    this.currentStep = { index: 1, name: '', id: 'InfoWrite', validateDataList:['fieldName','fieldAddress','buildingCount','areaTypeCount','houseHoldsCount'] };

    //    
    this.isDetailPopup = false; //    
    this.isPopupModifyMode = false; //    
    this.isNewFieldInfoPopup = false; //  
    this.newFieldInfoPopupType = ''; //  

    //  , , ,  Id
    this.selectedAreaTypeId = null;
    this.selectedBuildingId = null;
    this.selectedFloorId = null;
    this.selectedHouseHoldId = null;
    
    this.copyedData = {};//  
    this.selectedCopyData = [];//    
    this.deleteItemData = {}; //  

    // 
    this.buildingInfoData = new BuildingInfoModel();
    // 
    this.areaTypeInfoData = new AreaTypeInfoModel();
    //
    this.houseHoldInfoData = new HouseHoldInfoModel();
  }

  setModel(){
    this.model = new NewFieldModel();
    this.onAddManager();
  }

  // step - id 
  onChangeFieldWriteStep(changedId){
    this.currentStep = this.stepDataList.find(step => step.id == changedId)
  }

  // step - next step 
  onChangeFieldWriteNext(){
    if(!this.validated()) return;
    const nextIndex = this.currentStep.index+1;
    this.currentStep = this.stepDataList.find(step => step.index == nextIndex)
  }

  // () 
  validated(){
    //    
    if(!this.currentStep.validateDataList) return true;
    let isValidated = true;
    //  step    
    this.currentStep.validateDataList.forEach(validateData => {
      if(!this.model[validateData]){
        isValidated = false;
        return false;
      }
    });
    return isValidated;
  }

  //  - 
  onSearchAddress(value){
    this.isShowMap = true;
    console.log('%c  API GET',"color:blue;font-size:16px");
    console.log('  :',value);
  }

  //  -  
  onAddManager(){
    this.model.managerInfoList.push(new ManagerInfoModel());
  }

  //  -  
  onDeleteManager(deleteItem){
    this.model.managerInfoList = this.model.managerInfoList.filter(item => item != deleteItem);
  }

  //  - 
  newFieldInfoCompelete(){
    console.log('%c  API POST',"color:green;font-size:16px");
    console.log(JSON.parse(JSON.stringify(this.model.getData())));
    this.onAdd('building',this.model.buildingCount);
    this.onAdd('areaType',this.model.areaTypeCount);
    this.onChangeFieldWriteNext();
  }

  // , ,   
  onClickCloseNewFieldInfoPopup(type){
    if((this.isDetailPopup && !this.isPopupModifyMode) || !this.isDetailPopup){
      this.isNewFieldInfoPopup = false;
      this.newFieldInfoPopupType = '';
    }else{
      this.isPopupModifyMode = false;
    }
  }

  // , ,    || 
  onClickCompleteNewFieldInfoPopup(type){
    if(this.isPopupModifyMode){
      this.isNewFieldInfoPopup = false;
      this.isPopupModifyMode = false;
      this.newFieldInfoPopupType = '';
      this.setData(type,this[type+'InfoData']);
      if(type == 'building'){
        this.selectedBuildingId = this.buildingInfoData.id;
        this.onAdd('floor',this.buildingInfoData.groundFloor);
      }
    }else{
      this.isPopupModifyMode = true;
    }
  }

  //   
  onSelected(type, selectedId){
    switch(type){
      case 'building': {
        console.log('%c    API GET',"color:blue;font-size:16px");
        console.log(this.getTypeName(type)+' id :',selectedId);
        break;
      };
      case 'floor': {
        console.log('%c    API GET',"color:blue;font-size:16px");
        console.log(this.getTypeName(type)+' id :',selectedId);
        break;
      };
    }
    this.onDownSeletedNull(type);
    console.log(this.getSelectedId(type));
    this[this.getSelectedId(type)] = selectedId;
  }

  // 
  onEdit(type, selectedId){
    this.isDetailPopup = false;
    this.isPopupModifyMode = true;
    this.onViewPopup(type, selectedId);
  }

  //  
  onDetail(type, selectedId){
    this.isDetailPopup = true;
    this.isPopupModifyMode = false;
    this.onViewPopup(type, selectedId);
  }

  //   
  onViewPopup(type, selectedId){
    switch(type){
      case 'areaType': {
        var target = 'areaTypeInfoData';
        break;
      };
      case 'building': {
        var target = 'buildingInfoData';
        break;
      };
      case 'houseHold': {
        var target = 'houseHoldInfoData';
        break;
      };
    }
    const dataList = JSON.parse(JSON.stringify(this.getDataList(type)));
    this[target] = dataList.find(item => item.id == selectedId);
    this.isNewFieldInfoPopup = true;
    this.newFieldInfoPopupType = type;
    console.log('%c '+ this.getTypeName(type) +' API GET',"color:blue;font-size:16px");
    console.log(this.getTypeName(type)+' id :',selectedId);
  }

  // 
  onCopy(item){
    this.copyedData = item;
  }

  //  - 
  onPaste(type){
    const targetDataList = this.getDataList(type);
    const titleKeyName = this.getTitleKeyName(type);
    const copyedDataOrgin = Object.assign({}, JSON.parse(JSON.stringify(this.copyedData)));
    const copyedDataTitle = copyedDataOrgin[titleKeyName];
    // const pasteDataList = Object.assign({}, JSON.parse(JSON.stringify(this.selectedCopyData)));
    const copyDataList = [];
    this.selectedCopyData.forEach( selectedItem => {
      const targetIndex = targetDataList.findIndex(targetItem => targetItem.id == selectedItem.id);
      const targetId = selectedItem.id;
      const targetName = selectedItem[titleKeyName];
      const copyDataItem = Object.assign({}, copyedDataOrgin);

      copyDataItem.id = targetId;
      copyDataItem[titleKeyName] = targetName;

      if(type == 'floor'){
        var houseHoldDataList = JSON.parse(JSON.stringify(copyDataItem.houseHoldDataList ));
        houseHoldDataList.forEach(houseHoldData => {
          houseHoldData.houseHoldName = targetId+1+make00(houseHoldData.id+1)+'';
        });
        copyDataItem.houseHoldDataList = JSON.parse(JSON.stringify( houseHoldDataList ));
      }

      // console.log('  ');
      // console.log(JSON.parse(JSON.stringify(selectedItem)));
      // console.log(' ');
      // console.log(copyDataItem);
      // console.log('  ');
      // console.log(copyedDataOrgin);

      copyDataList.push(copyDataItem);

      targetDataList[targetIndex] = copyDataItem;
    });
    const params = {};
    params.copyData = copyedDataOrgin;
    params.pasetDataList = copyDataList;
    console.log('%c '+ this.getTypeName(type) +' API PUT',"color:orange;font-size:16px");
    console.log(params);
    const pasteDataTitleList = copyDataList.map(item => item[titleKeyName]);
    const pasteDataLength = copyDataList.length;
    if(pasteDataLength > 3){
      pasteDataTitleList.splice(3);
    }
    const pasteDataTitle = pasteDataLength > 3 ? `${pasteDataTitleList.join(', ')} ${pasteDataLength-3}` : pasteDataTitleList.join(', ');
    const toastMsg = `${copyedDataTitle} ${pasteDataTitle} .`;
    store.dispatch('START_TOAST_ACTION', toastMsg);
    this.selectedCopyData = [];
  }

  //   
  onAdd(type, idx){
    var terget = this.getDataList(type);
    const isPostOne = !idx;
    switch(type){
      case 'areaType': {
        if(terget.length == 10){
          alert('  10 ');
          return;
        }
        var pushedData = (i => { return new AreaTypeInfoModel(this.AreaTypeInfoViewModel.tempTypeNameList[i],i) });
        break;
      };
      case 'building': {
        var pushedData = (i => { return new BuildingInfoModel('1'+make00(i+1)+'',i) });
        break;
      };
      case 'floor': {
        var pushedData = (i => { return {id:i,floorName:(i+1)+'',houseHoldDataList:[]} });
        const building = this.model.buildingInfoDataList.find(item => item.id == this.selectedBuildingId);
        building.groundFloor = building.floorDataList.length+1;
        break;
      };
      case 'houseHold': {
        var pushedData = (i => { return new HouseHoldInfoModel((this.selectedFloorId+1)+make00(i+1)+'', i)});
        break;
      };
    }
    var firstIndex = 0;
    if(isPostOne){
      firstIndex = terget.length > 0 ? terget[terget.length-1].id+1 : 0;
      var idx = firstIndex+1;
    }else{
      terget.splice(0);
      console.log('%c '+ this.getTypeName(type) +'  API POST (     || backend )',"color:green;font-size:16px");
      console.log('   :',idx);
    };
    for(var i = firstIndex; i < idx; i++){
      terget.push(JSON.parse(JSON.stringify(pushedData(i))));
      if(isPostOne){
        console.log('%c '+ this.getTypeName(type) +'  API POST',"color:green;font-size:16px");
        console.log(JSON.parse(JSON.stringify(pushedData(i))));
      }
    }
  }

  //   
  onDelete(type, deleteId){
    var dataList = this.getDataList(type);
    const deleteIndex = dataList.findIndex(item => item.id == deleteId);
    const deleteTitle = dataList.find(item => item.id == deleteId)[this.getTitleKeyName(type)];
    dataList.splice(deleteIndex,1);
    console.log('%c '+ this.getTypeName(type) +' API DELETE',"color:red;font-size:16px");
    const upperSeleteName = this.getUpperSeleteName(type);
    if(this[this.getSelectedId(type)] == deleteId){
      this[this.getSelectedId(type)] = null;
      this.onDownSeletedNull(type);
    }

    console.log(' '+ this.getTypeName(type) +' id :',deleteId);
    store.dispatch('START_TOAST_ACTION', upperSeleteName + deleteTitle+' ');
  }

  //   
  onDeleteAll(type){
    var terget = this.getDataList(type);
    this[this.getSelectedId(type)] = null;
    this.onDownSeletedNull(type);
    console.log('%c '+ this.getTypeName(type) +'  API DELETE',"color:red;font-size:16px");
    console.log(' DATA -     ',JSON.parse(JSON.stringify(terget)));
    store.dispatch('START_TOAST_ACTION', this.getUpperSeleteName(type) + this.getTypeName(type)+' ');
    terget.splice(0);
  }

  onDownSeletedNull(type){
    switch(type){
      case 'building': {
        this.selectedFloorId = null;
        this.selectedHouseHoldId = null;
      };
      case 'floor': {
        this.selectedHouseHoldId = null;
      };
    }
  }

  //  
  getTypeName(type){
    switch(type){
      case 'areaType': {
        return '';
      };
      case 'building': {
        return '';
      };
      case 'floor': {
        return '';
      };
      case 'houseHold': {
        return '';
      };
    }
  }

  //   
  getUpperName(type){
    switch(type){
      case 'areaType': {
        return '';
      };
      case 'building': {
        return '';
      };
      case 'floor': {
        return '';
      };
      case 'houseHold': {
        return '';
      };
    }
  }

  //    
  getIsSeletedUpper(type){
    switch(type){
      case 'areaType': {
        return true;
      };
      case 'building': {
        return true;
      };
      case 'floor': {
        return Boolean(this.selectedBuildingId != null);
      };
      case 'houseHold': {
        return Boolean(this.selectedFloorId != null);
      };
    }
  }

  getUpperSeleteName(type){
    const buildingSeletedId = this[this.getSelectedId('building')];
    const floorSeletedId = this[this.getSelectedId('floor')];
    var buildingTitle = '';
    var floorTitle = '';
    if(buildingSeletedId && type != 'building'){
      // console.log('  id',buildingSeletedId);
      const building = this.getDataList('building');
      buildingTitle = building.find(item => item.id == buildingSeletedId)[this.getTitleKeyName('building')]+' ';
    }
    if(floorSeletedId && type == 'houseHold'){
      // console.log('  id',floorSeletedId);
      const floor = this.getDataList('floor');
      floorTitle = floor.find(item => item.id == floorSeletedId)[this.getTitleKeyName('floor')]+' ';
    }
    return buildingTitle + floorTitle
  }

  //    key name
  getTitleKeyName(type){
    switch(type){
      case 'areaType': {
        return 'areaTypeName';
      };
      case 'building': {
        return 'buildingName';
      };
      case 'floor': {
        return 'floorName';
      };
      case 'houseHold': {
        return 'houseHoldName';
      };
    }
  }

  //    
  getDataList(type){
    switch(type){
      case 'areaType': {
        return this.model.areaTypeDataList;
      };
      case 'building': {
        return this.model.buildingInfoDataList;
      };
      case 'floor': {
        if(this.selectedBuildingId == null) return [];
         const building = this.model.buildingInfoDataList.find(item => item.id == this.selectedBuildingId);
         if(!building) return [];
         return building.floorDataList;
      };
      case 'houseHold': {
        if(this.selectedFloorId == null) return [];
        const building = this.model.buildingInfoDataList.find(item => item.id == this.selectedBuildingId);
        const floor = building.floorDataList.find(item => item.id == this.selectedFloorId);
        if(!building || !floor) return [];
        return floor.houseHoldDataList;
      };
    }
  }

  //    
  setData(type, obj){
    const dataList = this.getDataList(type);
    const targetIndex = dataList.findIndex(item => item.id == obj.id);
    dataList[targetIndex] = obj;
    console.log('%c '+this.getTypeName(type)+' API PUT',"color:orange;font-size:16px");
    console.log(JSON.parse(JSON.stringify(obj)));
    store.dispatch('START_TOAST_ACTION', obj[this.getTitleKeyName(type)]+' ');
  }

  //    ID
  getSelectedId(type){
    switch(type){
      case 'areaType': {
        return 'selectedAreaTypeId';
      };
      case 'building': {
        return 'selectedBuildingId';
      };
      case 'floor': {
        return 'selectedFloorId';
      };
      case 'houseHold': {
        return 'selectedHouseHoldId';
      };
    }
  }

  //  ID 
  getAreaTypeName(id){
    const target = this.model.areaTypeDataList.find(areaType => areaType.id == id);
    if(!target) return false;
    return target.areaTypeName;
  }
}
